body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 10px 50px;
  display: flex;
}

.logo__link {
  line-height: 0;
}

.logo {
  width: 40px;
  height: 40px;
  margin: auto;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin: 0 10px;
  list-style: none;
}

.nav__item:first-child {
  margin-left: 0;
  padding-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
  text-decoration: underline 2px #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 10px;
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: -22.5px;
  left: 0;
}
